.header {
  width: 100%;
  z-index: var(--z-index-header);
  transition: background-color var(--main-transition), backdrop-filter var(--main-transition), box-shadow var(--main-transition);
  border-bottom: .5px solid var(--header-border-cl-20);
  position: fixed;
  top: 0;
  left: 0;
}

.header.no-transparency {
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background-color: rgba(12, 12, 12, .75);
