@media screen and (min-width: 768px) {
  .mobile-menu-open-icon {
    width: 32px;
    height: 32px;
  }
}

.mob-menu.is-open + .mobile-menu-open .buger {
  display: none;
}

.mob-menu.is-open + .mobile-menu-open .close-icon {
  display: inline;
