.nav-link {
  letter-spacing: .05em;
  color: var(--main-white-txt-cl);
  padding-bottom: 6px;
  font-size: 14px;
  line-height: 1.3;
  position: relative;
}

.nav-link:after {
  content: "";
  width: 0;
  height: 4px;
  background-color: var(--main-white-txt-cl);
  transition: width var(--main-transition), border-radius var(--main-transition);
  border-radius: 50%;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (min-width: 1440px) {
  .nav-link.current:after {
    width: 4px;
  }
}

.nav-link:hover:after, .nav-link:focus:after, .nav-link.current:hover:after, .nav-link.current:focus:after {
  width: 100%;
