    gap: 36px;
  }
}

.mob-menu-container .nav-link {
  font-size: 18px;
  line-height: 1.3;
}

@media screen and (min-width: 768px) {
  .mob-menu-container .nav-link {
    font-size: 28px;
    line-height: 1.14;
  }
}

.mob-menu-container .soc-media {
  gap: var(--soc-media-gap);
  align-self: flex-start;
  display: flex;
}

@media screen and (min-width: 768px) {
  .mob-menu-container .soc-media {
    display: none;
  }
